.searchWrapper{
   margin: auto;
   width: 1000px;
   background: #FFFFFF;
   border-radius: 20px;
   margin-bottom: 100px;
   .titleSearch{
      padding: 40px 0 20px 50px;
      h2{
         font-size: 30px;
         color: #333333;
         font-weight: 600;
      }
   }
   .search{
      padding: 20px 0 0 50px;
      input{ 
         position: absolute;
         border-radius: 20px;
         border: 1px solid #888888;
         height: 40px;
         width: 900px;
         font-weight: 100;
         font-size: 15px;
         color: #333333;
         padding: 0 0 0 60px ;
      }
      button{
         border: none;
         background-color: white;
         height: 5px;
         width: 5px;
         img{
            position: relative;
            padding: 8px 10px 8px 15px ;
         }
      }
   }
   .resultSearch{
      padding: 40px 0 80px 50px;
      h3{
         font-weight: 400;
         font-size: 20px;
         color: #333333;
      }
      h4{
         font-weight: 200;
         font-size: 20px;
         text-align:center;
         line-height: 40px;
      }
   }


}